import React from 'react';
import ReactTable from 'react-table'
import 'react-table/react-table.css'

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentData: []
        };
        this.ws = new WebSocket("ws://127.0.0.1:8888/");
    }

    render() {
        this.ws.onopen = () => {
            console.log('Opened Connection!')
        };

        this.ws.onmessage = (event) => {
            this.setState({ currentData: JSON.parse(event.data) });
        };

        this.ws.onclose = () => {
            console.log('Closed Connection!')
        };

        const columns = [
            { Header: 'CTimeStamp', accessor: 'CTimeStamp' },
            { Header: 'CallAveragePrice', accessor: 'CallAveragePrice' },
            { Header: 'CallBidQty', accessor: 'CallBidQty' },
            { Header: 'CallBidPrice', accessor: 'CallBidPrice' },
            { Header: 'CallAskQty', accessor: 'CallAskQty' },
            { Header: 'CallAskPrice', accessor: 'CallAskPrice' },
            { Header: 'CallOpen', accessor: 'CallOpen' },
            { Header: 'CallHigh', accessor: 'CallHigh' },
            { Header: 'CallLow', accessor: 'CallLow' },
            { Header: 'CallClose', accessor: 'CallClose' },
            { Header: 'CallVeta', accessor: 'CallVeta' },
            { Header: 'CallVolga', accessor: 'CallVolga' },
            { Header: 'CallColor', accessor: 'CallColor' },
            { Header: 'CallZomma', accessor: 'CallZomma' },
            { Header: 'CallSpeed', accessor: 'CallSpeed' },
            { Header: 'CallCharm', accessor: 'CallCharm' },
            { Header: 'CallVanna', accessor: 'CallVanna' },
            { Header: 'CallGamma', accessor: 'CallGamma' },
            { Header: 'CallVega', accessor: 'CallVega' },
            { Header: 'CallTheta', accessor: 'CallTheta' },
            { Header: 'CallDelta', accessor: 'CallDelta' },
            { Header: 'CallPrevIV', accessor: 'CallPrevIV' },
            { Header: 'CallVolume', accessor: 'CallVolume' },
            { Header: 'CallOIChange', accessor: 'CallOIChange' },
            { Header: 'CallOI', accessor: 'CallOI' },
            { Header: 'CallIV', accessor: 'CallIV' },
            { Header: 'CallLtpChange', accessor: 'CallLtpChange' },
            { Header: 'CallLtp', accessor: 'CallLtp' },
            { Header: 'Strike', accessor: 'Strike' },
            { Header: 'PutLtp', accessor: 'PutLtp' },
            { Header: 'PutLtpChange', accessor: 'PutLtpChange' },
            { Header: 'PutIV', accessor: 'PutIV' },
            { Header: 'PutOI', accessor: 'PutOI' },
            { Header: 'PutOIChange', accessor: 'PutOIChange' },
            { Header: 'PutVolume', accessor: 'PutVolume' },
            { Header: 'PutDelta', accessor: 'PutDelta' },
            { Header: 'PutTheta', accessor: 'PutTheta' },
            { Header: 'PutVega', accessor: 'PutVega' },
            { Header: 'PutGamma', accessor: 'PutGamma' },
            { Header: 'PutPrevIV', accessor: 'PutPrevIV' },
            { Header: 'PutVanna', accessor: 'PutVanna' },
            { Header: 'PutCharm', accessor: 'PutCharm' },
            { Header: 'PutSpeed', accessor: 'PutSpeed' },
            { Header: 'PutZomma', accessor: 'PutZomma' },
            { Header: 'PutColor', accessor: 'PutColor' },
            { Header: 'PutVolga', accessor: 'PutVolga' },
            { Header: 'PutVeta', accessor: 'PutVeta' },
            { Header: 'PutOpen', accessor: 'PutOpen' },
            { Header: 'PutHigh', accessor: 'PutHigh' },
            { Header: 'PutLow', accessor: 'PutLow' },
            { Header: 'PutClose', accessor: 'PutClose' },
            { Header: 'PutAskPrice', accessor: 'PutAskPrice' },
            { Header: 'PutAskQty', accessor: 'PutAskQty' },
            { Header: 'PutBidPrice', accessor: 'PutBidPrice' },
            { Header: 'PutBidQty', accessor: 'PutBidQty' },
            { Header: 'PutAveragePrice', accessor: 'PutAveragePrice' },
            { Header: 'FuturePrice', accessor: 'FuturePrice' },
            { Header: 'FutureOrFairPrice', accessor: 'FutureOrFairPrice' },
            { Header: 'FTimeStamp', accessor: 'FTimeStamp' }
        ]
        console.log(this.state.currentData);
        return (
            <div className="App">
        <ReactTable
          data={this.state.currentData}
          columns={columns}
        />
      </div>
        );
    }
}

export default App;